import { ActivatedRoute, Router } from "@angular/router";
import {
	ChangeDetectorRef,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { CoreMatTable, CoreMatTableInterface, FilterDateInterface, Page, PageRequest, Sort } from "./core-data-table";
import { animate, state, style, transition, trigger } from "@angular/animations";

import { CellsComponentList } from "./setting/CellsComponentRegistry";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { TableService } from "./table.service";
import { TranslateService } from './translate.service';
import { debounceTime } from "rxjs/operators";

interface displayColumnsConfig {
	sizeIcon?: number;
	displayYes?: boolean;
	displayNo?: boolean;
}

interface displayedColumnsInterface {
	key: string; // object key
	value: string; // display column values
	ratio?: number;
	note?: number;
	order?: number; // sort column
	class?: string; // css class
	module?: CellsComponentList;
	override?: string | string[]; // for building url or avatar name
	display?: string; // force displaying other stuff than element[focus]
	align?: string; // cell content align 'left center right'
	sort?: boolean; // 'sortable column'
	clickable?: boolean; // enable clickable column when sorting is disable
	statement?: boolean; // by default statement is false and is used with 'clickable' options
	valueStatement?: string[];
	subTitle?: string;
	addClass?: string;
	emptyStarUrl?: string;
	starUrl?: string;
	subTitleFormat?: string;
	config?: displayColumnsConfig;
	valueOverride?: {
		[key: string]: string
	};
	fontSize?: string;
}

@Component({
	selector: 'ngx-design-table',
	templateUrl: './table.component.html',
	styleUrls: ['./table.component.scss'],
	animations: [trigger('detailExpand', [
		state('collapsed', style({ height: '0px', minHeight: '0', display: 'none' })),
		state('expanded', style({ height: '*' })),
		transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)'))
	])],
	encapsulation: ViewEncapsulation.None
})
class TableComponent implements OnInit, OnChanges, OnDestroy {
	@ViewChild('MatPaginatorCurrent', { static: true }) paginatorCurrent: MatPaginator;
	@ViewChild('table', { static: true }) sortCurrent: MatSort;
	@Input() columnDefinitions: [displayedColumnsInterface];
	@Input() displayDetail: boolean = false;
	@Input() displayComponent: string;
	@Input() data: CoreMatTableInterface;
	@Input() rowMargin: string = '0px 0px';
	@Input() lang: string;
	@Input() btnOverride = false;

	@Input() loading = false;
	@Output() callFunction: EventEmitter<any> = new EventEmitter<any>();
	@Output() onReady: EventEmitter<boolean> = new EventEmitter<boolean>(true)
	@Input() inputSearch = '';
	@Input() EmptyRow = false;
	@Input() blockDetails = false;
	@Output() clicked: EventEmitter<{ key: string, statement: boolean }> = new EventEmitter();

	public columnsToDisplay: string[];
	public filter: Array<string> = [];
	public displayedColumns: any;
	public expandedElement: any;
	public index = 0;
	public open = '';
	public search = '';
	public cancelSearch = '';
	public noResult = '';
	public details = '';
	public showTable = false;
	public isViewInit = false;
	public style: any = null;

	private PrivateColumnDefinitions: [displayedColumnsInterface];
	private previousPageNumber: number = null;
	private previousSearch: string = null;
	private previousFilter !: any;
	private previousFilterDate!: any;
	constructor(private router: Router,
		private route: ActivatedRoute,
		private service: TableService,
		private detector: ChangeDetectorRef,
		private translate: TranslateService,
		private changeDetectorRef: ChangeDetectorRef) {
		this.onReady.emit(false);
	}

	expand(element) {
		if (this.blockDetails) {
			return;
		}
		if (this.expandedElement === element) {
			this.expandedElement = null;
		} else {
			this.expandedElement = element
		}

	}

	async ngOnInit(): Promise<void> {


	}

	ngOnDestroy() {

	}

	ngAfterViewChecked() {
		this.showTable = true;
	}

	buildHeaders() {
		this.displayedColumns = this.sort();


		if (this.displayedColumns) {
			const tmp: any = [];
			for (let k of this.displayedColumns) {
				tmp.push(k.key);
			}
			this.columnsToDisplay = [...tmp];
		}
		//console.log('Module Table New Update Column Definition',  this.columnsToDisplay);
	}

	generateClass(Class: string[]) {
		const MyClass: string[] = ['default-table-class'];
		for (let c of Class) {
			if (c && c !== '') {
				MyClass.push(c);
			}
		}
		return MyClass;
	}

	sort() {
		const compare = (a: any, b: any) => {
			return (a.order < b.order ? -1 : (a.order > b.order ? 1 : 0))
		};
		if (this.PrivateColumnDefinitions) {
			return [...this.PrivateColumnDefinitions.sort(compare)];
		}
	}

	public buildLink(override: string[], element) {
		if (override.length >= 2) {
			let basePath: string = override[0];
			for (let i = 1; i < override.length; i++) {
				basePath += '/' + element[override[i]];
			}
			return basePath;
		}
	}

	public Join(elem: any, override: string[], joinKey: string = '\n'): string {
		const value: string[] = [];
		for (let x in elem) {
			if (override.indexOf(x) > -1) {
				value.push(elem[x]);
			}
		}
		return value.join(joinKey)
	}

	reset() {
		this.data.filter({
			target: {
				value: ''
			}
		});
		return true;
	}

	expandShow(template: string) {

	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.data) {
			this.pageNumberSub();
		}
		if (changes && changes.data && changes.data.firstChange) {
			this.init();
			setTimeout(() => {
				this.isViewInit = true;
			}, 10000);
		} else {
			if ((this.inputSearch.length > 3)
				&& this.inputSearch.length < 200) {
				if (this.data) {
					console.log('avant next 0')
					this.data.filter(this.inputSearch);
					this.data.pageNumber.next(0)
					this.data.fetch(0);
					this.changeDetectorRef.markForCheck();
				}
			}
		}
	}

	init() {
		this.open = this.translate.translate(this.lang, 'OPEN');
		this.search = this.translate.translate(this.lang, 'SEARCH');
		this.cancelSearch = this.translate.translate(this.lang, 'CANCEL_SEARCH');
		this.noResult = this.translate.translate(this.lang, 'NO_RESULT');
		this.details = this.translate.translate(this.lang, 'DETAILS');

		if (this.rowMargin) {
			this.style = {
				borderSpacing: this.rowMargin
			}
		}

		if (this.data && this.columnDefinitions) {
			this.PrivateColumnDefinitions = this.columnDefinitions;
			this.displayedColumns = this.sort();
			this.columnsToDisplay = this.displayedColumns.map((e: any) => e.key);
			this.expandedElement = false;
			this.data.paginator = this.paginatorCurrent;
			this.data.sort = this.sortCurrent;
			const page = this.route.snapshot.queryParams["page"];
			if (page) {
				this.data.pageNumber.next(page);
			} else {
				this.data.pageNumber.next(1);
			}

			const currentPage: number = page ? Number(page) - 1 : 0;
			if (this.data.paginator) {
				this.data.paginator.pageIndex = currentPage;
			}
			this.data.startWith = currentPage;
			this.data.fetch(currentPage);
			this.data.number = currentPage;
			this.data.page$.pipe(debounceTime(500))
				.subscribe((n: any) => {
					this.onReady.emit(true)
				})
			this.service.updateHeader.subscribe((status: boolean) => {
				if (status === true) {
					this.displayedColumns = null;
					this.columnsToDisplay = null;
					this.PrivateColumnDefinitions = this.service.displayColumn;
					this.displayedColumns = this.sort();
					this.columnsToDisplay = this.displayedColumns.map((e: any) => e.key);
					this.detector.detectChanges();
				}
			});
		}
	}

	pageNumberSub() {
		if (this.data) {
			this.data.pageNumber.subscribe((newpage: number) => {
				if (newpage > 0 && newpage != this.previousPageNumber) {
					this.previousPageNumber = newpage
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { page: newpage + 1 },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
				} else if (newpage === 0 && newpage != this.previousPageNumber) {
					this.previousPageNumber = newpage;
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { page: null },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
					this.changeDetectorRef.markForCheck();
				}
				if (this.data && this.data.paginator && this.data.paginator.pageIndex !== newpage) {
					this.data.paginator.pageIndex = newpage;
					this.changeDetectorRef.markForCheck();
				}
			});
			this.data.search.subscribe((newsearch: string) => {
				if (newsearch !== this.previousSearch) {
					this.previousSearch = newsearch
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { search: newsearch },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
				} else if (newsearch == '' && newsearch !== this.previousSearch) {
					this.previousSearch = newsearch;
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { search: null },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
					this.changeDetectorRef.markForCheck();
				}
			})

			this.data.pageFilter.subscribe((data: any) => {
				if (data !== this.previousFilter) {
					this.previousFilter = data
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { ...data },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
				} else if ((!data || Object.keys(data).length) && data !== this.previousFilter) {
					const clear = {}
					Object.keys(this.previousFilter).forEach((key: string) => clear[key] = null)
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: {
								...clear
							},
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
					this.previousFilter = data;
					this.changeDetectorRef.markForCheck();
				}
			})


			this.data.pageFilterDate.subscribe((data: any) => {
				if (data !== this.previousFilter) {
					this.previousFilterDate = data
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: { ...data },
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
				} else if ((!data || Object.keys(data).length) && data !== this.previousFilterDate) {
					const clear = {}
					Object.keys(this.previousFilterDate).forEach((key: string) => clear[key] = null)
					this.router.navigate(
						[],
						{
							relativeTo: this.route,
							queryParams: {
								...clear
							},
							queryParamsHandling: 'merge' // remove to replace all query params by provided
						});
					this.previousFilterDate = data;
					this.changeDetectorRef.markForCheck();
				}
			})
		}
	}


}

export {
	CoreMatTable,
	FilterDateInterface,
	CoreMatTableInterface,
	Page,
	PageRequest,
	Sort,
	displayedColumnsInterface,
	CellsComponentList,
	TableComponent
}
