import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'lib-with-note-cell',
  templateUrl: './with-note-cell.component.html',
  styleUrls: ['./with-note-cell.component.css']
})
export class WithNoteCellComponent implements OnInit {
  @Input() title: string;
  @Input() note: number;
  @Input() class: string;
  @Input() emptyStarUrl: string;
  @Input() starUrl: string;
  @Input() lang: string;
  @Input() id: string;
  @Output() callHandler: EventEmitter<any> = new EventEmitter<any>();
  stars = [1, 2, 3, 4, 5];
  tempNote = 0;
  constructor() { }

  ngOnInit(): void {
    // if (!this.emptyStarUrl) {
    //   this.stars = this.stars.slice(0, this.note)
    // }
  }

  handleVote(note: number, event: string) {
    if (!this.note) {
      if (event === 'enter') {
        this.tempNote = note;
      } else if (event === 'click') {
        if (Number(this.note) < 1) {
          this.callHandler.emit({ _id: this.id, Type: 'note', note })
        }
      } else {
        this.tempNote = 0
      }
    }
  }
  // handleVote(note: number) {
  //   if (Number(this.note) < 1) {
  //     this.callHandler.emit({ _id: this.id, Type: 'note', note })
  //   }
  // }

  tooltip(lang: string) {
    return lang === 'fr' ? 'Laissez une note' : 'Leave a note'
  }
  getNote(note: number) {
    return `Note ${note}`
  }

}
