import {BehaviorSubject, Observable, Subject, from} from 'rxjs';
import {debounceTime, pluck, share, startWith, switchMap} from 'rxjs/operators';

import {DataSource} from '@angular/cdk/collections';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';

export interface Sort {
    active: string;
    direction: 'asc' | 'desc';
}

export interface PageRequest {
    page: number;
    size: number;
    sort?: Sort;
}

export interface Page {
    content: any;
}

export interface CoreMatTableInterface {
    page$: Observable<any>;
    totalElements: number;
    search: BehaviorSubject<string>;
    paginator: MatPaginator;
    number: number;
    data: any[];
    size: number;
    fetch: (page: any) => void;
    connect: () => void;
    disconnect: () => void;
    sort: MatSort;
    sortIt: (sortidea: any) => void;
    filter: (myFilter: any) => void;
    filterData: (data: any, filter: any) => void
    filterDate: (dateFilter: FilterDateInterface) => void;
    setData: (data: Array<any>) => void,
    pageNumber: BehaviorSubject<number>;
    startWith: number;
    pageFilter: BehaviorSubject<any>;
    pageFilterDate: BehaviorSubject<FilterDateInterface>;
    update: () => void // trigger update after set data
    getRaw: () => any[];
}


export interface FilterDateInterface {
    active: string;
    valueStart: Date;
    valueEnd: Date;
}


export class CoreMatTable extends DataSource<Element> {
    public page$: Observable<Page>;
    public totalElements = 0;
    public number = 0;
    public size: any;
    public sort: MatSort;
    public paginator: MatPaginator;
    public data: any;
    public pageNumber: BehaviorSubject<number> = new BehaviorSubject<number>(null);
    public startWith = 0;
    private _update = new BehaviorSubject(new Date().getTime());
    private pageSort: BehaviorSubject<Sort>;
    public pageFilter: BehaviorSubject<any>;
    public pageFilterDate: BehaviorSubject<FilterDateInterface>;
    private _totalElements = new BehaviorSubject(0);
    public search: BehaviorSubject<string>;
    private backUpData: any;
    private emptyRow = false;
    private filterTable = {};
    private dataAfterSearch;
    private currentPage: number = 0;

    constructor(data: any, sortRules: Sort,
                rangeRules: FilterDateInterface, size = 20, detailRaws: boolean = true,
                emptyRow: boolean = false, filterT: any = {}, startswith = 0) {
        super();
        this.startWith = startswith;
        this.size = size;
        this.data = [...data];
        this.dataAfterSearch = [];
        this.backUpData = [...data];
        //this.totalElements = data.length;
        this.emptyRow = emptyRow;
        this.filterTable = filterT;
        this.pageSort = new BehaviorSubject<Sort>(sortRules);
        this.pageFilterDate = new BehaviorSubject<FilterDateInterface>(null);
        this.pageFilter = new BehaviorSubject<any>('');
        //  this.pageNumber = new BehaviorSubject<number>(this.startWith);
        this.search = new BehaviorSubject('');
        this._totalElements.subscribe((page: number) => this.totalElements = page);

        this.page$ = this._update.pipe(switchMap(triggerUpdate => this.pageSort.pipe(
            switchMap(sortAction => this.pageFilter.pipe(debounceTime(500))
                .pipe(switchMap(filter => this.search.pipe(
                    switchMap(search => this.pageFilterDate.pipe(
                        switchMap((range: any) => {
                                this._totalElements.next(this.data.length);
                                return this.pageNumber.pipe(
                                    startWith(this.startWith),
                                    switchMap(page => from([{
                                        content: this.slice(
                                            this.sortData(
                                                this._search(
                                                    search,
                                                    this.filterData(
                                                        this.filterDateRange(this.data, range),
                                                        filter)
                                                ), sortAction
                                            ), page, this.size, detailRaws)
                                    }])), share());
                            }
                        ))
                    ))))))));
        this.pageNumber.next(this.startWith);
    }

    filterDateRange(data: any, range: FilterDateInterface) {
        if (data && data.length && range && range.active && ((range.valueEnd && range.valueStart) || (range.valueEnd && !range.valueStart) || (!range.valueEnd && range.valueStart))) {
            return data.filter((e: any) => {
                if (range.valueStart && range.valueEnd) {
                    return new Date(e[range.active]) >= new Date(range.valueStart)
                        && new Date(e[range.active]) <= new Date(range.valueEnd);
                } else if (range.valueStart && !range.valueEnd) {
                    return new Date(e[range.active]) >= new Date(range.valueStart);
                } else if (!range.valueStart && range.valueEnd) {
                    return new Date(e[range.active]) <= new Date(range.valueEnd);
                }
            });
        } else {
            return data;
        }
    }

    update() {
        this._update.next(new Date().getTime());
    }

    getRaw() {
        return this.backUpData;
    }

    ponderation(str: string, searchKey: string) {
        let stack: string[] = str.split(' ');
        let pond: number = 0;
        for (let s of stack) {
            let search: string = s;
            if (search && search.includes(searchKey)) {
                pond++;
            }
        }
        return pond;
    }

    filterData(data: any, filter: any) {
        if (typeof filter === 'object' && Object.keys(filter).length) {
            delete filter.inputSearch;
            data = this.filterDataObject(data, filter);
        }
        this.dataAfterSearch = data;
        return data;
    }

    private _search(filter, data) {
        const result = [];
        if (filter && filter.replace(/[^a-zA-Z ]/g, ' ')) {
            for (let e of data) {
                e.pond = 0;
                const dataRaw: string = JSON.stringify(e).toLowerCase()
                    .replace(/[^a-zA-Z0-9 ]/g, ' ');
                const stack: string[] = filter.toLowerCase().replace(/[^a-zA-Z0-9 ]/g, ' ')
                    .split(' ').filter((e) => e && e !== '');
                let combination: number = 0;
                for (let k of stack) {
                    if (dataRaw.includes(k)) {
                        e.pond += 1;
                        combination++;
                    }
                }
                if (e.pond >= 1 && combination === stack.length) {
                    result.push(e);
                }
            }
            return result.filter((e => e.pond > 0)).sort((a, b) => a > b ? 1 : (a < b ? -1 : 0));
        } else {
            return data;
        }
    }

    formatCharacters(text) {
        return String(text).toLowerCase().replace(/[^a-zA-Z0-9 ]/g, '');
    }

    filterDataObject(data: any, filter: any) {
        const result: any[] = [];
        if (data && data.length && filter && Object.keys(filter).length > 0) {
            for (let e of data) {
                let ok = true;
                e.pond = 0;
                Object.keys(filter).forEach(key => {
                    try {
                        if (Array.isArray(filter[String(key)]) && (filter[String(key)].includes(e[String(key)]) || filter[String(key)].length === 0) || this.formatCharacters(filter[String(key)]) === this.formatCharacters(e[String(key)])) {
                            //e.pond += 1;
                        } else {
                            //e.pond = 0;
                            ok = false;
                        }
                    } catch (e) {
                        console.log(e);
                    }
                });
                //if (e.pond) {
                if (ok) {
                    result.push(e);
                }
            }
            return result;
        } else {
            return data;
        }
    }

    setData(data: any[], override : boolean = false) {
        this.data = [...data];
        if (override) this.backUpData = [...data];
        this.update();
        //  this.pageNumber.next(0);
    }

    sortData(data: any, sortAction: any) {
        if (sortAction.direction !== '' && data && data.length) {
            return data.sort((a: any, b: any) => {
                return this.compare(a[sortAction.active], b[sortAction.active], sortAction.direction === 'desc');
            });
        } else {
            return data;
        }
    }

    compare(a: number | string | any[], b: number | string | any[], isAsc: boolean) {
        if (Array.isArray(a)) {
            return (((Array.isArray(a) ? a.length : a) > ((Array.isArray(b) ? b.length : b)) ? -1 : ((Array.isArray(b) ? b.length : b)) > ((Array.isArray(a) ? a.length : a)) ? 1 : 0) * (isAsc ? -1 : 1));
        } else {
            if (a === null && b !== null) {
                return isAsc ? -1 : 1;
            } else if (a !== null && b === null) {
                return isAsc ? 1 : -1;
            } else if (a === null && b === null) {
                return 0;
            } else {
                return isAsc ? (a > b ? -1 : (a === b ? 0 : 1)) : ((a > b ? -1 : (a === b ? 0 : 1))) * -1;
            }
        }
    }

    fetch(page: number): void {
        this.pageNumber.next(page);
    }

    sortIt(sortidea: any): void {
        this.pageSort.next(sortidea);
    }

    filter(myFilter: any): void {
        if (myFilter != undefined) {
            this.pageFilter.next(myFilter);
            this.checkTotalPage();
        }
    }

    filterDate(dateFilter: FilterDateInterface): void {
        this.pageFilterDate.next(dateFilter);
        this.checkTotalPage();
    }

    connect(): Observable<any> {
        return this.page$.pipe(pluck('content'));
    }

    disconnect(): void {
    }

    slice(data: any[], start: number = 0, end: number = 20, detailRow: boolean = true): any {
        this._totalElements.next(data.length);
        const rows = data.length ? data.slice(start * end, (start * end) + end) : [];
        return rows;
    }

    checkTotalPage() {
        // const totalPageNumber = this.totalElements > this.size ? Math.ceil((this.totalElements / this.size)) : 0;
        this.fetch(0);

    }
}
